        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true
            this.active1=false
        },
        yes(){
            this.active=false
            this.active1=true
        },
        lookDetail(index){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/leaveDetail',query:{active:this.active}})
        },
        issue(){

        }
    }
}
